#about {
	margin: 0 140px;

	.name {
		width: 100%;
		display: none;

		h1 {
			font-size: 12vw;
			text-transform: uppercase;
			line-height: 1;
			// -webkit-text-stroke: 3px white;
			color: #466ec7;
			// color: white;
		}

		.two {
			width: 100%;
			display: flex;
			justify-content: space-between;
		}
	}
	.biography {
		display: flex;
		display: none;

		.description {
			p {
				margin: 30px 0 30px 5vw;
			}
		}
	}
	.skills {
		.skills-title {
			margin: 50px 0;

			h3 {
				text-transform: uppercase;
			}
		}

		.skill-card {
			margin-bottom: 50px;

			h4 {
				margin-bottom: 15px;
			}
		}
	}
}

// 1600
@media screen and (max-width: 1200px) {
	.biography-image {
		display: none;
	}

	.biography {
		.description {
			p {
				margin: 30px 0px !important;
			}
		}
	}
}

//1577
@media screen and (max-width: 1577px) {
	#about {
		margin: 0 140px 0 30px !important;
	}
}

// 1176
@media screen and (max-width: 1176px) {
	.skills {
		.skill-card {
			margin-bottom: 50px;
		}
	}
}

// 1030
@media screen and (max-width: 1030px) {
	#about {
		margin: 0 140px 0 30px !important;

		.skills {
			.skill-card {
				margin-bottom: 50px;
			}
		}
	}
}

@media screen and (max-width: 850px) {
	#about {
		margin-right: 30px !important;
	}
}
