.container {
	display: flex;
	// justify-content: space-around;

	.image {
		position: relative;
		width: 55vw;
		display: table;
		overflow: hidden;
		visibility: hidden;

		.cover {
			background-color: #160622;
			display: table-cell;
			position: absolute;
			bottom: 0;
			right: 0;
			width: 100%;
			height: 100%;
			z-index: 1;
		}

		img {
			display: table-cell;
			width: 55vw;
			// box-shadow: 0 0 20px;
			overflow: hidden;
			visibility: hidden;
		}
	}

	.text {
		display: flex;
		align-items: center;
		visibility: hidden;

		div {
			.title {
				font-size: 5.5rem;
				font-weight: bold;
				letter-spacing: 5px;
				margin-bottom: 30px;
				color: #466ec7;
			}
		}
	}
}

@media screen and (max-width: 1350px) {
	.container {
		flex-direction: column !important;
		justify-content: start;

		.image {
			width: 100%;

			img {
				margin-bottom: 25px;
				width: 100%;
			}
		}

		.text {
			margin: 0 !important;

			.title {
				margin-bottom: 5px !important;
			}
		}
	}
}

// 1176
@media screen and (max-width: 1176px) {
	.image {
		img {
			margin-bottom: 0px;
		}
	}

	.title {
		font-size: 4.5rem !important;
	}
}

//-------------

@media screen and (max-width: 1030px) {
	.title {
		font-size: 4rem !important;
	}
}

@media screen and (max-width: 662px) {
	.title {
		font-size: 3.5rem !important;
	}
}

@media screen and (max-width: 589px) {
	.title {
		font-size: 3rem !important;
	}
}

@media screen and (max-width: 530px) {
	.title {
		font-size: 2.5rem !important;
	}
}

@media screen and (max-width: 430px) {
	.title {
		font-size: 2.3rem !important;
	}
}
