#first-page {
	height: 100vh;

	.background {
		position: absolute;
		top: 0;
	}

	.first-page-container {
		position: absolute;
		height: 100vh;
		width: 100vw;
		top: 0;
		display: flex;
		justify-content: space-between;
		align-items: center;
		color: #466ec7;

		.icons {
			font-size: 70px;
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;

			a {
				color: #466ec7;
				text-decoration: none;

				&:hover {
					color: white;
				}
			}

			i {
				margin: 30px;
			}

			p {
				font-family: "Product Sans", sans-serif;
				font-size: 50px !important;
				font-weight: bold;
			}
		}

		.name {
			padding-right: 10vw;

			h1 {
				font-family: "Product Sans", sans-serif;
				font-size: 20rem;
				height: 200px;
				line-height: 1;
				font-weight: 700;
				letter-spacing: 2px;
				overflow: hidden;
				position: relative;

				.h1 {
					position: absolute;
					z-index: 200;
					top: 200px;

					span {
						position: relative;
					}
				}

				.h2 {
					position: absolute;
					z-index: 200;
					top: 200px;

					span {
						position: relative;
					}
				}

				.h3 {
					position: absolute;
					z-index: 200;
					top: 200px;

					span {
						position: relative;
					}
				}
			}

			.p {
				font-size: 3.6rem;
				height: 36px;
				position: relative;
				overflow: hidden;
				line-height: 1;
				color: #466ec7;

				.h4 {
					// position: absolute;
					// z-index: 200;
					// top: 36px;

					span {
						position: relative;
					}
				}
			}

			p {
				position: absolute;
				font-size: 3.6rem;
				color: transparent;
			}
		}
	}
}

@media screen and (max-width: 950px) {
	#first-page {
		h1 {
			font-size: 17rem !important;
			height: 17rem !important;

			.h1 {
				top: 17rem !important;
			}

			.h2 {
				top: 17rem !important;
			}

			.h3 {
				top: 17rem !important;
			}
		}

		.p {
			font-size: 3.1rem !important;
			margin-top: 10px;

			p {
				font-size: 3.1rem !important;
			}
		}
	}
}

@media screen and (max-width: 710px) {
	#first-page {
		h1 {
			font-size: 14rem !important;
			height: 14rem !important;

			.h1 {
				top: 14rem !important;
			}

			.h2 {
				top: 14rem !important;
			}

			.h3 {
				top: 14rem !important;
			}
		}

		.p {
			font-size: 2.6rem !important;
			margin-top: -5px !important;

			p {
				font-size: 2.6rem !important;
			}
		}
	}
}

@media screen and (max-width: 615px) {
	#first-page {
		.icons {
			display: none !important;
		}

		.name {
			margin: 0 30px;
		}
	}
}

