.cursor {
	position: fixed;
	z-index: 1000;
	// position: relative;

	.main-cursor {
		z-index: 1001;
		position: fixed;
		border-radius: 50%;
		width: 10px;
		height: 10px;
		background-color: white;
		pointer-events: none;
		overflow: hidden;
		transform: translate(50%, 50%);
		text-align: center;
	}

	.sec-cursor {
		z-index: 1000;
		position: fixed;
		top: 10px;
		left: 10px;
		border-radius: 50%;
		width: 30px;
		height: 30px;
		background-color: white;
		opacity: 0.5;
		pointer-events: none;
		overflow: hidden;
		transform: translate(0, 0, 0);
		transition-duration: 150ms;
	}
}

@media screen and (max-width: 662px) {
	.cursor {
		display: none;
	}
}
