@font-face {
	font-family: "Monument";
	src: url("Monument/Monument-Regular.otf") format("opentype");
	font-weight: normal;
	font-style: normal;
}

@font-face {
	font-family: "Monument";
	src: url("Monument/Monument-Ultrabold.otf") format("opentype");
	font-weight: bold;
	font-style: normal;
}

@font-face {
	font-family: "Monument";
	font-weight: lighter;
	src: url("//db.onlinewebfonts.com/t/9b310b2d91dfaf3b73ac6ea66c8351b2.eot");
	src: url("//db.onlinewebfonts.com/t/9b310b2d91dfaf3b73ac6ea66c8351b2.eot?#iefix")
			format("embedded-opentype"),
		url("//db.onlinewebfonts.com/t/9b310b2d91dfaf3b73ac6ea66c8351b2.woff2")
			format("woff2"),
		url("//db.onlinewebfonts.com/t/9b310b2d91dfaf3b73ac6ea66c8351b2.woff")
			format("woff"),
		url("//db.onlinewebfonts.com/t/9b310b2d91dfaf3b73ac6ea66c8351b2.ttf")
			format("truetype"),
		url("//db.onlinewebfonts.com/t/9b310b2d91dfaf3b73ac6ea66c8351b2.svg#Monument Extended Ultralight")
			format("svg");
}

@font-face {
	font-family: "Product Sans";
	src: url("product-sans/Product-Sans-Regular.ttf");
	font-weight: normal;
	font-style: normal;
}

@font-face {
	font-family: "Product Sans";
	src: url("product-sans/Product-Sans-Bold.ttf");
	font-weight: bold;
	font-style: normal;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	&::selection {
		color: black;
		background: white;
	}
	scroll-behavior: smooth;
	cursor: none;
}

html {
	font-size: 62.5%;
	overflow-x: hidden;
}

body {
	background-color: #160622;
	// color: #79248a;
	color: rgb(254, 254, 254);
	// background-color: #010001;
	// background-color: #fe3939;
	// font-family: "Archivo Black", sans-serif;
	// font-family: "Quicksand", sans-serif;
	font-family: "Monument";
}

h1 {
	color: #466ec7;
}

h2 {
	color: #466ec7;
}

h3 {
	color: #466ec7;
	font-size: 4.5rem;
	letter-spacing: 5px;
}

h4 {
	font-size: 3.5rem;
	letter-spacing: 3px;
	color: #466ec7;
}

h5 {
	color: #466ec7;
}

h6 {
	color: #466ec7;
}

p {
	font-size: 1.8rem;
	letter-spacing: 1px;
	line-height: 1.7;
	font-weight: lighter;
	// font-family: "Product Sans", sans-serif;
}

