#contact {
	position: relative;
	div {
		.contact-text {
			margin: 100px 140px;

			h3 {
				color: #fefefe;
				// position: relative;
				overflow: hidden;

				.hidden-text1 {
					position: relative;
					overflow: hidden;
					top: 55px;

					span {
						position: relative;
						overflow: hidden;
						top: 55px;
					}
				}
			}

			.lighter-h3 {
				font-weight: lighter;

				.hidden-text2 {
					position: relative;
					overflow: hidden;
					top: 55px;

					z-index: 200;
					// top: 15px;

					span {
						position: relative;
						overflow: hidden;
						top: 55px;
					}
				}
			}
		}

		.contact-links {
			background-color: #466ec7;
			width: 60vw;
			margin: 100px 0;
			display: flex;
			justify-content: center;
			align-items: center;

			.div {
				display: flex;
				justify-content: start;
				// align-items: flex-start;

				margin: 50px 0;
				div {
					position: relative;
					display: flex;
					flex-direction: column;
					align-items: center;
					justify-content: center;
					margin: 0 10px;

					p {
						font-size: 1.4rem;

						a {
							text-decoration: none;
							color: white;
						}
					}

					.border {
						width: 0;
						height: 1px;
						background-color: white;
						z-index: 300;
					}
				}
			}
		}

		.footer {
			margin: 20px 0s !important;
			font-size: 1.4rem !important;
			text-align: center;
		}
	}
}

@media screen and (max-width: 1577px) {
	.contact-text {
		margin-left: 30px !important;
	}
}

@media screen and (max-width: 1100px) {
	.contact-links {
		width: 100vw !important;
	}
}

@media screen and (max-width: 850px) {
	.contact-text {
		margin-right: 30px !important;
	}

	.div {
		p {
			font-size: 1.2rem !important;
		}
	}

	.footer {
		font-size: 1.2rem !important;
	}
}

@media screen and (max-width: 625px) {
	.contact-links {
		.div {
			margin: 0 auto !important;
			flex-direction: column;
			margin: 16px 0 !important;

			p {
				padding: 3px 0;
			}

			.border0 {
				max-width: 300px;
			}

			.border1 {
				max-width: 75px;
			}

			.border2 {
				max-width: 60px;
			}

			.border3 {
				max-width: 26px;
			}
		}
		// flex-direction: column;
	}
}
