nav {
	height: 8vh;
	display: flex;
	justify-content: space-between;
	align-items: baseline;
	/* color: #ff005a; */
	/* color: #8400ff; */
	/* color: rgb(243, 243, 9); */
	/* color: #466ec7; */
	color: #c992f0;
	color: #dbdbdb;
	font-size: 3.5rem;
	margin: 0 auto;
	position: -webkit-sticky;
	position: sticky;
	top: -1px;
	z-index: 12;

	.menu {
		text-transform: uppercase;
		font-size: 1.9rem;
	}

	.nav-links {
		background-color: #160622;
		color: white;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		// position: absolute;
		// right: 0;
		// height: 100vh;
		// transform: translateX(100%);
		// transition: transform 0.5s ease-in;
		z-index: 12;

		li {
			list-style: none;
			margin-left: 25px;
			color: #dbdbdb;
			font-size: 2.5rem;
			display: none;
		}
	}
}

.test {
	width: 100vw;
	padding: 35px 35px;
	display: flex;
	justify-content: space-between;
}

@media screen and (max-width: 850px) {
	.test {
		padding-top: 20px !important;
		padding-bottom: 55px !important;
		background-image: linear-gradient(#160622 55px, transparent);
	}
}
